@import url('https://fonts.googleapis.com/css?family=Lato');

*{
    font-family: 'Lato', sans-serif;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    --main-text-color: rgb(66, 66, 66);
    --secondary-text-color: rgb(103, 103, 103);
    --tetriary-text-color: rgb(255, 255, 255);
    --hover-text-color: rgb(15, 21, 33);
    --main-color-hover: rgb(99, 171, 150)
}

.outer{
    padding-top: 6%;
}

.innerRow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

.innerColumn{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: start;
}

.avatar{
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin: 50px 50px;
    margin-left: auto;
}

.info{
    width: 50%;
    text-align: center;
}

h1 {
    width: 100vw;
    color: var( --main-text-color);
    font-size: 42px;
}

.social {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-align: center;
        align-items: center;
}

.social > div {
    width: 100px;
    display: -ms-flexbox;
    display: flex;
    margin: 0 5px;
}

.social a {
    padding: 10px;
    margin: 0 10px;
    width: 50px;
    height: 50px;
    border-radius: 15px;
    text-decoration: none;
    color: var(--tetriary-text-color);
    background-color: var(--main-color);
    -webkit-transition: 0.1s ease-in;
    -o-transition: 0.1s ease-in;
    transition: 0.1s ease-in;
}

.social a:hover {
    background-color: var(--main-color-hover);

}

.position{
    width: 100%;
    margin: 25px 0;
    color: var(--secondary-text-color);
    border-bottom: 2px solid var(--tetriary-text-color);
    font-size: 22px;
}

.about {
    width: 100%;
    color: var(--secondary-text-color);
}

.skills {
   display: -ms-flexbox;
   display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.contacts{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 25px;
}

.contacts__link {
    border: none;
    text-align: center;
    font-size: 16px;
    padding: 5px 25px;
    border-radius: 15px;
    text-decoration: none;
    color: var(--tetriary-text-color);
    background-color: var(--main-color-hover);
    -webkit-transition: 0.1s ease-in;
    -o-transition: 0.1s ease-in;
    transition: 0.1s ease-in;
    cursor: pointer;
}

.contacts__link:hover {
    color: var(--tetriary-text-color);
    background-color: var(--main-color-hover);
}



