.outer{
    padding-top: 6%;
}

.skills {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.skills__item {
    margin: 20px 35px;
    width: 200px;
    font-size: 16px;
    overflow: hidden;
}

.skills__text {
    margin: 5px 25px;
}

.skills__progressBackground{
    width: 100%;
    height: 15px;
    background-color: var(--main-color);
    border-radius: 15px;
}

.skills__progressFill{
    width: 70%;
    height: 15px;
    background-color: var(--progress-color-orange);
    border-radius: 15px;

}

.skills__progressFill.orange{
    background-color: var(--progress-color-orange);
}

.skills__progressFill.red{
    background-color: var(--progress-color-red);
}

.skills__progressFill.green{
    background-color: var(--progress-color-green);
}

.skills__progressFill.blue{
    background-color: var(--progress-color-blue);
}

.skills__progressFill.purple{
    background-color: var(--progress-color-purple);
}

.skills__progressFill:after {
    content: "";
    background: #f1f1f1;
    display: block;
    border-radius: 15px;
    padding-top: 11%;
    opacity: 0;
    -webkit-animation: color-me-in 1s infinite;
            animation: color-me-in 1s infinite;
}

@-webkit-keyframes color-me-in {
    0% {
        -webkit-transform: translateX(-150px);
                transform: translateX(-150px);
        opacity: 1;
        -webkit-transition: 1s;
        -o-transition: 1s;
        transition: 1s;
    }
    100% {
        -webkit-transform: translateX(-0px);
                transform: translateX(-0px);
        opacity: 0;
        -webkit-transition: 0.7s ease-out;
        -o-transition: 0.7s ease-out;
        transition: 0.7s ease-out;
    }
}

@keyframes color-me-in {
    0% {
        -webkit-transform: translateX(-150px);
                transform: translateX(-150px);
        opacity: 1;
        -webkit-transition: 1s;
        -o-transition: 1s;
        transition: 1s;
    }
    100% {
        -webkit-transform: translateX(-0px);
                transform: translateX(-0px);
        opacity: 0;
        -webkit-transition: 0.7s ease-out;
        -o-transition: 0.7s ease-out;
        transition: 0.7s ease-out;
    }
}



